html{
  text-align: center;
  text-transform: capitalize;
  background-image: url("./back.png");
  background-size: cover;
  height: 100%;
}

nav div.nav-wrapper{
  position: relative;
  height: 100px;
}

nav div img.logo{
  width: 80px;
  height: 80px;
  margin: 10px;
}

.card{
  position: relative;
  top: 80px;
  padding: 30px;
  height: 450px;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
}

.input-field input[type=text]:focus + label {
  color: yellow;
}

Input:focus{
  border-bottom: 1px solid yellow !important;
  -webkit-box-shadow: 0 1px 0 0 yellow !important;
          box-shadow: 0 1px 0 0 yellow !important;
}

div div.card-image{
  width: 60%;
  margin-left: 20%;
}


